.moreAboutMeContainer {
   /* border: 2px solid red; */
   /* width: 200vh; */
}

.moreAboutMeContent {
   /* border: 2px solid green; */
   margin: 10px 0px;
   padding: 4px;
   width: 100%;
   height: 89vh;
   overflow-x: hidden;
   overflow-y: auto;
   /* text-align: justify; */
}

.moreAboutMeCard {
   /* border: 2px solid blue; */
   display: flex;
   justify-content: space-around;
   margin: 10px 0px;
   align-items: center;
   height: 27vh;
}

.moreAboutMeCard img {
   width: 10rem;
   /* border: 1px solid green; */
   filter: saturate(60%);
   border-radius: 7px;
}

.moreAboutMeCard img:hover {
   width: 10rem;
   /* border: 1px solid green; */
   filter: saturate(100%);
   transition: filter 0.5s;
}

.cardRightContent {
   /* border: 1px solid black; */
   /* text-align: justify; */
   width: 80%;
   padding: 0px 5px;
}

.text {
   /* border: 2px solid green; */
   width: 80%;
   padding: 0px 5px;
}

.text h3 {
   font-weight: 400;
}

.text p {
   font-family: var(--poppinFont);
}

.moreAboutMeCard:nth-of-type(even) {
   text-align: right;
   /* border: 2px solid red; */
   display: flex;
   justify-content: space-around;
   flex-direction: row-reverse;
   width: 100%;
}

.moreAboutMeCard:nth-of-type(even) .cardRightContent {
   text-align: right;
   /* border: 2px solid rgb(0, 0, 0); */
   display: flex;

   justify-content: space-around;
   flex-direction: row-reverse;
   width: 88%;
}

.moreAboutMeCard:nth-of-type(even) .cardRightContent .text {
   width: 90%;
   /* border: 1px solid red; */
   display: flex;
   justify-content: center;
   flex-direction: column;
}

.moreAboutMeCard:nth-of-type(even) .cardRightContent .text p {
   text-align: left;
}

::-webkit-scrollbar {
   width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
   background: rgb(223, 223, 223);
   border-radius: 3px;
}

/* Handle */
::-webkit-scrollbar-thumb {
   background: rgb(160, 160, 160);
   border-radius: 3px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
   background: #747474;
}

@media only screen and (max-width: 1000px) {
   .moreAboutMeContainer {
      /* border: 2px solid red; */
   }

   .moreAboutMeCard {
      /* border: 2px solid blue; */
      display: flex;
      justify-content: space-evenly;
      flex-wrap: wrap;
      height: 70vh;
      align-items: center;
      /* padding: px !important; */
   }

   .moreAboutMeCard img {
      display: none;
      /* width: 100%;
    height: 50vh; */
   }

   .moreAboutMeCard img:hover {
      /* display: none; */
      width: 100%;
      height: 50vh;
   }

   .cardRightContent {
      /* border: 2px solid green; */
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      /* box-shadow: 0px 0px 2px black; */
      border-radius: 5px;
   }
   .cardRightContent:hover {
      filter: saturate(80%);
   }

   .text {
      /* border: 2px solid rgb(12, 12, 12); */
      display: flex;
      justify-content: center;
      flex-direction: column;
      width: 80%;
      height: 80%;
      color: rgb(228, 228, 228);
   }

   .moreAboutMeCard:nth-of-type(even) .cardRightContent {
      text-align: left;
      /* border: 2px solid rgb(255, 0, 0); */
      display: flex;
      justify-content: space-around;
      flex-direction: row-reverse;
      width: 100%;
   }

   .moreAboutMeCard:nth-of-type(even) .cardRightContent .text p {
      text-align: left;
   }

   .moreAboutMeCard:nth-of-type(even) .cardRightContent .text {
      width: 80%;
   }

   /* -----------backgrounds----- */
   .musicBackground {
      background: linear-gradient(rgba(0, 0, 0, 0.074), rgba(0, 0, 0, 0.66)),
         url("../../Asserts/images/musicEnthusiest.jpg") no-repeat center/cover;
      background-attachment: fixed;
      filter: saturate(10%);
   }
   .hodophileBackground {
      background: linear-gradient(rgba(0, 0, 0, 0.074), rgba(0, 0, 0, 0.924)),
         url("../../Asserts/images/travelerRaj2.jpg") no-repeat center/cover;
      background-attachment: fixed;
      filter: saturate(10%);
   }
   .bookBackground {
      background: linear-gradient(rgba(0, 0, 0, 0.074), rgba(0, 0, 0, 0.924)),
         url("../../Asserts/images/bookLover.jpg") no-repeat center/cover;
      background-attachment: fixed;
      filter: saturate(10%);
   }

   .friendlyBackground {
      background: linear-gradient(rgba(0, 0, 0, 0.074), rgba(0, 0, 0, 0.924)),
         url("../../Asserts/images/friendlyNature.jpg") no-repeat center/cover;
      background-attachment: fixed;
      filter: saturate(10%);
   }

   .problemBackground {
      background: linear-gradient(rgba(0, 0, 0, 0.074), rgba(0, 0, 0, 0.924)),
         url("../../Asserts/images/problemSolverRaj.jpg") no-repeat center/cover;
      background-attachment: fixed;
      filter: saturate(10%);
   }
}
